import axios from "axios";

const instance = axios.create({
  baseURL: "https://youtube.googleapis.com/youtube/v3/",
  params: {
    key: "AIzaSyBWqhobrT6nFXBQyYjuJcNg6IgsoCy0i48",
  },
});

instance.interceptors.response.use(
  function (response) {
    return response.data ? response.data : response;
  },
  function (error) {
    if (error.response) {
      // X l li t response HTTP
      const { status, data } = error.response;

      // Thng bo li cho ngi dng nu cn thit
      if (status === 401) {
        // X l li xc thc, chng hn chuyn hng n trang ng nhp
        console.log("Unauthorized error:", data);
      } else {
        // Hin th thng bo li cho ngi dng
        console.log("An error occurred:", data);
      }
    } else if (error.request) {
      // X l li khng c response t server
      console.log("No response received from server:", error.request);
    } else {
      // X l li khc (li trong qu trnh gi request)
      console.log("Error in request:", error.message);
    }

    // Ghi log h thng
    // logErrorToServer(error);

    // Tr v mt object cha thng tin li
    return {
      data: error.response ? error.response.data : null,
      status: error.response ? error.response.status : null,
      headers: error.response ? error.response.headers : null,
    };
  }
);

export default instance;
